@import "main-bx";

#model-slider {
    position: -webkit-sticky; // required for Safari
    position: sticky;
    top: 82px; // required as well.
    height: 84px;
    z-index: 1001;
    @include below(1370px) {
        top: 62px;
    }

    @include below(1024px) {
        top: 134px;
    }
}

.model-tab {
    display: flex;
    justify-content: space-between;

    @include below(769px) {
        display: none;
    }

    &__btn {
        flex: 1 0 auto;
        padding: 21px 0;

        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #FFFFFF;
        cursor: pointer;

        background: #30363A;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
        transition: all .2s linear;

        &:first-child {
            font-weight: 300;
            font-size: 20px;
            line-height: 18px;
            letter-spacing: 2px;
            text-transform: uppercase;
            color: #CACACA;
        }

        &:hover {
            background-color: $white;
            color: $color_burlywood;
        }

        &:not(:last-child) {
            span {
                display: block;
                border-right: 1px solid rgba(255, 255, 255, 0.2);
            }
        }

        &.active {
            background-color: $white;
            color: $color_burlywood;
        }
    }
}