@import "main-bx";

.news {
    padding: 112px 0;
    background-color: #F2F2F2;

    &__flex {
    }

    &__title {
        font-weight: 300;
        font-size: 60px;
        line-height: 60px;
        text-transform: uppercase;
        margin: 0 auto 70px auto;
        width: 100%;
        text-align: center;
    }

    &__cards {
        display: flex;
        justify-content: space-between;

        &-item {
            position: relative;
            width: 32%;
            height: auto;
            min-height: 400px;
            cursor: pointer;

            .item-img {
                position: absolute;
                left: 0;
                top: 0;
                object-fit: cover;
                width: 100%;
                height: 100%;
                z-index: 1;
            }
            .item-text {
                position: absolute;
                bottom: 0;
                padding: 30px;
                z-index: 2;

                .item-title {
                    margin-bottom: 15px;
                    font-size: 16px;
                    color: #fff;
                }
                .item-date {
                    font-size: 13px;
                    color: #cacaca;
                }
            }
            .item-href {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: 3;
            }
        }
    }
    @include below(1366px) {
        &__title {
            font-size: 40px;
        }
    }

    @include below(1250px) {
        padding: 75px 0;

        &__cards {
            display: block;

            &-item {
                height: 400px;
                width: 400px;
                margin: 0 20px 20px 0;

                &.slick-slide {
                    min-height: 400px;
                }

                .item-text {
                    padding: 20px;
                }
            }
        }
    }

    @include below(768px) {
        padding: 40px 0;

        &__title {
            margin-bottom: 40px;
            font-size: 28px;
        }

        &__cards {
            &-item {
                height: 300px;
                width: 300px;

                .item-text {
                    background-color: #fff;
                    min-height: 130px;
                    
                    .item-title {
                        font-size: 14px;
                        color: #30363a;
                    }

                    .item-date {
                        font-size: 11px;
                        color: #30363a;
                    }
                }
            }
        }
    }
}