@import "main-bx";

.form {
    display: flex;
    align-items: center;
    position: relative;
    &__input {
        font-family: $font_Montserrat;
        width: 270px;
        padding: 18px 24px;
        background-color: $white;
        font-size: 16px;
        border: 2px solid #DEE2E6;
        &::placeholder {
            color: $color_grey;
        }

        &.select__input {
          -webkit-appearance: none;
          cursor: pointer;
          color: $color_grey;

          &:hover {
            color: rgb(27, 27, 27);
          }
        }
    }

    

    &__error {
        position: absolute;
        bottom: -30px;
        left: 0;
        font-size: 14px;
        color: #CA0012;
        padding: 7px 0;
        width: 100%;
        max-width: 270px;
        text-align: center;
        background-color: rgba(250, 250, 250, .7);
        user-select: none;

        opacity: 0;

        transition: all .2s linear;
    }

    &.error-validation {
        .form__error {
           opacity: 1;
        }

        .form__input {
            border-color: #CA0012;
        }
    }

    &.disabled {
        .lds-ellipsis{
            display: inline-block;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 100;
        }

        .btn span {
            opacity: 0;
        }

        .btn img {
            display: none;
        }
    }

    &_modal {
        flex-direction: column;

        .form__error {
            top: -30px;
            bottom: auto;
            background-color: transparent;
            max-width: max-content;
            text-align: center;
        }

        .form__box,
        .form__input,
        .btn {
            width: 100%;
        }
    
        .form__box {
            margin-bottom: 22px;
        }

        .form__input {
            border: 2px solid #DEE2E6;
        }
    }
}


.lds-ellipsis {
    display: none;
    position: relative;
    width: 80px;
    height: 80px;
    margin: 0 auto;
  }
  .lds-ellipsis div {
    position: absolute;
    top: 23px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #fff;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  .lds-ellipsis div:nth-child(1) {
    left: 8px;
    animation: lds-ellipsis1 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(2) {
    left: 8px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(3) {
    left: 32px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(4) {
    left: 56px;
    animation: lds-ellipsis3 0.6s infinite;
  }
  @keyframes lds-ellipsis1 {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
  }
  @keyframes lds-ellipsis3 {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0);
    }
  }
  @keyframes lds-ellipsis2 {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(24px, 0);
    }
  }
  