$base_font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$accent_font: "Tactic-Round", $base_font;
$accent_font2: "Tactic-Round-Exd", $base_font;

$font_Montserrat: "Montserrat", $base_font;

$font_sans: "Tactic-Sans", $base_font;

$black: #000000;
$white: #fff;

$color_dark: #1B1D23;
$color_burlywood: #C78E66;
$color_grey: #ADB5BD;

