@import "main-bx";

.footer-center {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 43px 0;
    flex-wrap: wrap;

    border-bottom: 1px solid rgba(255, 255, 255, .2);

    &__left {
        display: flex;
        align-items: flex-start;
        margin-bottom: 20px;
    }

    &__tel {
        margin-right: 45px;

        a {
            font-weight: normal;
            font-size: 20px;
            line-height: 20px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: #fff;

            &:hover {
                opacity: .7;
            }
        }
    }

    &__text {
        font-weight: 500;
        font-size: 12px;
        line-height: 18px;
        color: #CACACA;
        margin-bottom: 10px;
        max-width: 365px;
    }
}

@include below(1080px) {
    .footer-center {
        &__left {
            width: 100%;
            margin-bottom: 20px;
        }
    }
}

@include below(768px) {
    .footer-center {
        padding: 30px 0;
        &__left {
            flex-direction: column;
            margin-bottom: 30px;
        }

        &__tel {
            margin-bottom: 30px;
        }
    }
}