@import "main-bx";

.gallery {
    border-top: 0.5px solid #b3b3b3;

    @include below(769px) {
        border-top: 0;
    }

    &__title {
        font-weight: 300;
        font-size: 60px;
        line-height: 60px;

        text-align: center;
        text-transform: uppercase;

        padding: 100px 0;

        @include below(769px) {
            font-size: 28px;
            line-height: 30px;
            padding: 40px 0 30px;
        }
    }

    &__images {
        display: flex;
        flex-wrap: wrap;
    }

    &__img {
        &_50 {
            width: 50%;

            @include below(769px) {
                display: none;
            }
        }

        &_25 {
            width: 25%;

            @include below(769px) {
                width: 50%;
            }
        }

        img {
            height: 100%;
        }
    }
}