@import "main-bx";

.features {
    position: absolute;
    padding-right: 140px;

    @include below(769px) {
        position: static;
        padding-right: 0;
    }



    &__item {
        &:not(:last-child) {
            border-bottom: 1px solid #CACACA;
        }
    }

    &__head {
        display: flex;
        align-items: center;
        cursor: pointer;
        padding: 20px 0;

        &.active {
            h6 {
                color: #C78E66;
            }

            img {
                transform: rotate(0);
            }
        }

        &:hover {
            h6 {
                color: #C78E66;
            }
        }

        h6 {
            font-weight: bold;
            font-size: 16px;
            line-height: 18px;

            text-align: left;
            transition: all .25s linear;
        
        }

        img {
            width: 18px;
            height: 18px;
            margin-left: 10px;
            transform: rotate(-90deg);
            transition: all .25s linear;
        }
    }

    &__body {
        max-height: 0;
        overflow: hidden;
        transition: all .25s linear;

        p {
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #30363A;
        }

        &.active {
            padding-bottom: 20px;
            max-height: 50rem;
            overflow: visible;
        }
    }
}