@import "main-bx";

.mfp-content .modal {
    display: block;
}



.success-error {
    position: fixed;
    top: 10%;
    left: 0;
    right: 0;
    z-index: 2000;

    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;

    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    text-align: center;
    font-size: 1rem;
    border-radius: 5px;
    padding: .75rem 1.25rem;
    box-shadow: -1px -1px 14px 0px rgba(0,0,0,0.75);

    display: none;
}

.modal {
    display: none;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;

    .mfp-close {
        display: none !important;
    }

    &_second {
        max-width: 1260px;

        @include below(map-get($breaks, mobile)) {
            .modal__img {
                display: none;
            }
        }
    }

    .btn {
        border: 1px solid #30363A;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $color_dark;
        
        svg {
            width: 18px;
            height: 18px;
            fill: $color_burlywood;
            margin-left: 20px;
        }
        &:hover {
            border: 1px solid $color_burlywood;
            background-color: $color_burlywood;
            color: $white;

            svg {
                fill: $white;
            }
        }

        &:active {
            border: 1px solid #30363A;
            background-color: $white;
            color: #30363A;

            svg {
                fill: $color_burlywood;
            }
        }

        &:disabled svg {
            display: none;
        }
    }

    &__img {
        img {
            height: 100%;
        }
    }

    &__wrap {
        display: flex;
    }

    &__inner {
        width: 100%;
        max-width: 500px;
        margin: 0 auto;
        position: relative;        
        background-color: $white;
        padding: 45px 50px 40px;
        text-align: center;

        @include below(map-get($breaks, mobile)) {
            max-width: 100%;
            padding: 25px 20px 40px;
            text-align: left;

            .form_modal .form__box {
                margin-bottom: 10px;
            }

            .btn {
                padding: 16px 0;
            }
        }
    }

    &__logo {
        display: block;
        width: 100%;
        max-width: 160px;
        margin: 0 auto;
        margin-bottom: 34px;
        @include below(map-get($breaks, mobile)) {
            max-width: 150px;
            margin-bottom: 10px;
        }
    }

    &__title {
        font-family: $font_Montserrat;
        font-weight: 500;
        font-size: 24px;
        line-height: 29px;
        margin-bottom: 20px;

        @include below(map-get($breaks, mobile)) {
            margin-bottom: 10px;
        }
    }

    &__text {
        font-size: 16px;
        line-height: 19px;
        margin-bottom: 30px;
        @include below(map-get($breaks, mobile)) {
            margin-bottom: 20px;
        }
    }

    &__policy {
        font-family: $accent_font2;
        font-weight: normal;
        font-size: 8px;
        line-height: 10px;
        text-align: left;

        &:last-child {
            margin-top: 10px;

            @include below(map-get($breaks, mobile)) {
                margin-top: 0;
            }
        }

        a {
            color: $color_burlywood;
            text-decoration: underline;
        }
    }

    &__close {
        position: absolute;
        top: 0;
        right: 0;

        padding: 12px;
    
        cursor: pointer;

        img {
            max-width: 28px;
            height: 28px;

            @include below(map-get($breaks, mobile)) {
                max-width: 15px;
                height: 14px;
            }
        }
    }
}

#success-modal {
    @media (max-width: 767px) {
        text-align: center;

        .modal__title {
            text-align: center;
        }
        .modal__text {
            text-align: center;
            br {
                display: none;
            }
        }

        .modal__logo {
            margin-left: auto;
            margin-right: auto;
        }
    }
}