@import "main-bx";

.comment {
    background-color: #F2F2F2;
    padding-bottom: 160px;
    &__title {
        font-weight: 300;
        font-size: 40px;
        line-height: 40px;
        text-align: center;
        text-transform: uppercase;

        padding: 100px 0;

        @media (max-width: 768px)  {
            padding: 40px 0;
            font-size: 30px;
            line-height: 30px;
        }
    }

    &__wrap {
        display: flex;
        padding: 0 112px;

        @media (max-width: 1080px) {
            padding: 0 20px;
        }

        @media (max-width: 768px) {
            padding: 0;
        }


        .slick-slide {
            margin: 0 20px;

            @media (max-width: 768px) {
                margin: 0;
                margin-right: 20px;
            }
        }
    }

    &__item {
        @media (max-width: 768px) {
            width: 280px;
        }
    }
    &__img {
        height: 270px;
        img {
            height: 100%;
        }
        @media (max-width: 768px) {
            height: 140px;
        }
    }
    &__content {
        padding: 30px;
        background-color: $white;
        height: 270px;
        overflow: hidden;
        @media (max-width: 768px) {
            height: 160px;
            padding: 20px;
        }
    }

    &__user {
        font-weight: normal;
        font-size: 30px;
        line-height: 40px;

        text-transform: uppercase;
        margin-bottom: 5px;

        @media (max-width: 768px) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    &__model {
        font-weight: normal;
        font-size: 18px;
        line-height: 18px;
        color: #7D8082;
        margin-bottom: 20px;

        @media (max-width: 768px) {
            font-size: 12px;
            line-height: 14px;
            margin-bottom: 15px;
        }
    }

    &__text {
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;

        @media (max-width: 768px) {
            font-size: 12px;
            line-height: 16px;
         }
    }
}


@include below(1366px) {
    .comment {
        padding-bottom: 130px;
        &__title {
            padding: 70px 0;
            font-size: 40px;
            line-height: 40px;
        }
    }
}

@include below(768px) {
    .comment {
        padding-bottom: 30px;
    }
}