@import "main-bx";

.btn {
    position: relative;
    font-family: $font_Montserrat;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: $white;
    text-transform: uppercase;
    padding: 16px 0;
    border: 1px solid #FFFFFF;
    cursor: pointer;
    transition: all .2s linear;
    letter-spacing: 2px;
    text-transform: uppercase;

    @include below(769px) {
        font-size: 11px;
        line-height: 18px;
    }

    &:hover {
        background-color: $white;
        color: $color_burlywood;

        .lds-ellipsis div {
            background-color: #ccc;
        }
    }
    &:active {
        background-color: transparent;
        color: $white;
    }

    &_second {
        background-color: $white;
        color: $color_dark;
        border: 1px solid $color_dark;
        &:hover {
            border: 1px solid $color_burlywood;
            background-color: $color_burlywood;
            color: $white;
        }
    }

    &_skew {
        background-color: transparent;
        border: none;

        position: relative;
        font-size: 24px;
        line-height: 29px;
        padding: 6px 32px;
        span {
            position: sticky;
            z-index: 2;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            width: 100%;
            height: 100%;
            background-color: $color_burlywood;
            border: 1px solid $color_burlywood;
            transform: skewX(-25deg);
            transition: all .2s linear;
        }

        &:hover::before {
            background-color: $white;
        }

        &.active {
            color: $color_burlywood;
            &::before {
                background-color: $white;
            }

            &:hover {
                opacity: 0.8;
            }
        } 
    }

    &[disabled="disabled"] {
        position: relative;
        cursor: no-drop;
        &:hover {
            color: $white;
            background-color: $color_burlywood;
        }
    }
}



