@import "main-bx";

.test-auto {
    @include block-bg('../img/content-v0.1/bg2.jpg');
    
    &_lx {
        background-image: url('../img/lx/lx-bg3.jpg') !important;
        position: relative;
        .benefits__btn {
            max-width: 370px;
        }

        @include below(768px) {
            background-image: url('../img/lx/mlx-bg3.jpg') !important;
        }

        .test-auto__text {
            @include below(768px) {
                display: none;
            }
        }

        .test-auto__inner  .btn {
            @include below(768px) {
                max-width: max-content;
                padding: 11px 20px;
                margin: 0 auto;
            }
        }

        .test-auto__inner {
            @include below(768px) {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }
        }
    }

    &.not-webp {
        background-image: url('../img/content-v0.1/bg2.jpg');
    }

    height: 1000px;

    font-family: $font_Montserrat;
    color: $white;
    &__container {
        height: 100%;

        @include below(768px) {
            padding-top: 78px;
            padding-bottom: 30px;
        }

        @include below(400px) {
            padding-top: 78px;
            padding-bottom: 30px;
        }
    }
    &__inner {
        display: flex;
        flex-direction: column;
        margin-right: auto;
        justify-content: center;
        width: 100%;
        max-width: 400px;
        height: 100%;

        &_model {
            max-width: 550px;
        }

        .form__box {
            &:first-child {
                margin-bottom: 30px;
            }
            &:nth-child(2) {
                margin-bottom: 60px;
            }
        }
        .form__input  {
            background-color: rgba(255, 255, 255, 0.1);
            border: 1px solid #7D8082;
            color: $white;
        }
    }

    &__title {
        font-weight: 300;
        font-size: 60px;
        line-height: 60px;

        text-transform: uppercase;
        margin-bottom: 60px;
    }

    &__text {
        font-weight: normal;
        font-size: 18px;
        line-height: 32px;
        margin-bottom: 60px;
    }

    &.about-block {
        @include block-bg('../img/content-v0.1/about.jpg');

        .test-auto__inner {
            max-width: 800px;
            padding: 10px;
        }
    }

    &__anchor {
        @include below(768px) {
            display: none;
        }
    }
}

@include below(1366px) {
    .test-auto {
        height: 688px;

        &__title {
            font-size: 40px;
            line-height: 40px;
            margin-bottom: 40px;
        }

        &__inner {
            .form__input {
                padding: 12px 16px;
                font-size: 12px;
                line-height: 18px;
            }
        }
    }
}

@include below(map-get($breaks, mobile)) {
    .test-auto {
        padding: 40px 0 5 0px;
        @include block-bg('../img/content-v0.1/mbg2.webp');

        &.not-webp {
            background-image: url('../img/content-v0.1/mbg2.jpg');
        }
        height: 568px;
        &__inner {
            align-items: center;
            max-width: 280px;
            margin: 0 auto;

            .form {
                width: 100%;
            }

            .form__box {
                &:nth-child(2) {
                    margin-bottom: 30px;
                }
            }
            .btn {
                padding: 16px 30px;
                margin: auto;
                img {
                    margin-top: 0;
                }
            }
        }

        &.about-block {
            .test-auto__text {
                margin-bottom: 45px;
            }
            .btn {
                margin: 0 auto 0 0;
            }
        }
        

        &__title {
            font-size: 30px;
            line-height: 30px;
            margin-bottom: 30px;
            text-align: center;
        }
    }
}