@import "main-bx";

.footer {
    padding: 40px 112px;
    background-color: #30363A;
    &__logo {
        max-width: 130px;
        margin-right: 40px;
    }
}

@include below(1366px) {
    .footer {
        padding: 45px 80px 40px;
    }
}

@include below(768px) {
    .footer {
        padding: 30px 20px;

        &__logo {
            position: relative;
            padding-bottom: 15px;
            margin-bottom: 15px;
            border: none;
            &::before {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                width: 30px;
                height: 1px;
                background: #C78E66;
            }
        }
    }
}

@import "./footer-top";
@import "./footer-center";
@import "./footer-social";
@import "./footer-bottom";