@import "main-bx";

.intro {
    background-image: url(../img/content-v0.1/bg1.webp);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 1080px;

    font-family: $accent_font2;
    color: $white;

    position: relative;


    &.not-webp {
        background-image: url(../img/content-v0.1/bg1.jpg);
    }

    &_vx {
        background-image: url(../img/content-v0.1/vx.jpg) !important;

        &.not-webp {
            background-image: url(../img/content-v0.1/vx.jpg) !important;
        }

        .intro__suptitle,
        .intro__title {
            color: $white;
        }

        .intro__anchor {
            bottom: 60px;
        }

        .intro__btn {
            margin-top: 10px;
            display: flex;
            align-items: center;
            padding: 16px 30px;
            border-color: $white;

            &:hover {
                border-color: #C78E66;

                svg {
                    fill: $white;
                }
            }

            svg {
                width: 10px;
                height: 18px;
                margin-left: 20px;
                fill: #C78E66;
            }
        }

    }

    &_txl {
        background-image: url(../img/content-v0.1/txl.jpg) !important;

        &.not-webp {
            background-image: url(../img/content-v0.1/txl.jpg) !important;
        }

        .intro__suptitle,
        .intro__title {
            color: $white;
        }

        .intro__anchor {
            bottom: 60px;
        }

        .intro__btn {
            margin-top: 10px;
            display: flex;
            align-items: center;
            padding: 16px 30px;
            border-color: $white;

            &:hover {
                border-color: #C78E66;

                svg {
                    fill: $white;
                }
            }

            svg {
                width: 10px;
                height: 18px;
                margin-left: 20px;
                fill: #C78E66;
            }
        }

    }

    &_lx {
        background-image: url(../img/lx/lx-bg1.jpg) !important;
        font-family: $font_Montserrat !important;
        background-attachment: fixed;
        height: 1000px;

        .intro__title {
            font-size: 70px;
            line-height: 70px;
            color: #fff;
            margin-bottom: 40px;

            @include below(1399px) {
                font-size: 55px;
                line-height: 55px;
                margin-bottom: 20px;
            }

            @include below(768px) {
                font-size: 40px;
                line-height: 40px;
                margin-bottom: 10px;
            }
        }

        .intro__content {
            padding-top: 340px;
            text-align: left;
            @include below(1399px) {
                padding-top: 200px;
            }

            @include below(768px) {
                display: flex;
                height: 100%;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;
                padding: 30px 0 40px !important;
            }
        }

        .intro__button {
            position: relative;
            display: block;
        }

        .intro__btn {
            padding: 16px 30px;
            display: flex;
            align-items: center;

            &:hover {
                background-color: #C78E66;
                color: #fff;
                border-color: #C78E66;
                
                svg {
                    fill: #fff;
                }
            }
            svg {
                fill: #C78E66;
                margin-left: 20px;
                width: 18px;
                height: 18px;
            }
        }

        .intro__anchor {
            @include below(768px) {
                display: none;
            }
        }
    }

    &__content {
        padding-top: 110px;
        text-align: center;
        color: #181B1D;
    }

    &__cost {
        font-weight: normal;
        font-size: 35px;
        line-height: 35px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #C78E66;
        margin-bottom: 40px;

        @include below(1399px) {
            font-size: 25px;
            line-height: 25px;
            margin-bottom: 20px;
        }

        @include below(768px) {
            margin-bottom: auto;
            font-size: 20px;
            line-height: 20px;
        }
    }

    &__suptitle {
        font-weight: normal;
        font-size: 30px;
        line-height: 30px;

        letter-spacing: 2px;
        text-transform: uppercase;

        margin-bottom: 10px;
    }

    &__title {
        font-weight: 300;
        font-size: 80px;
        line-height: 80px;
        text-transform: uppercase;
    }

    &__anchor {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 100;
        display: flex;
        justify-content: center;

        a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 60px;
            height: 60px;
            background: #30363A;
            transition: all .2s linear;

            &:hover {
                background: #282c2e;
            }
        }

        svg {
            width: 18px;
            height: 18px;
        }
    }
}


.bounce {
    -moz-animation: bounce 2s infinite;
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
}

@keyframes bounce {

    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(-5px);
    }

    60% {
        transform: translateY(-5px);
    }
}

@include below(1601px) {
    .intro {
        height: 890px;

        &__content {
            padding-top: 70px;
        }

        &__suptitle {
            font-size: 24px;
            line-height: 24px;
        }

        &__title {
            font-size: 60px;
            line-height: 60px;
        }
    }
}

@include below(1366px) {
    .intro {
        background-image: url(../img/content-v0.1/dbg1.webp);

        height: 700px;

        &.not-webp {
            background-image: url(../img/content-v0.1/dbg1.jpg);
        }

        &__anchor {
            a {
                width: 40px;
                height: 40px;
            }

            svg {
                width: 14px;
                height: 14px;
            }
        }
    }
}

@include below(1024px) {
    .intro {
        &__content {
            padding-top: 40px;
        }
    }
}

@include below(768px) {
    .intro {
        background-image: url(../img/content-v0.1/mbg1.jpg);
        height: 568px;
        &.not-webp {
            background-image: url(../img/content-v0.1/mbg1.jpg);
        }


        &_txl {
            background-image: url(../img/content-v0.1/mtxl.jpg) !important;

            &.not-webp {
                background-image: url(../img/content-v0.1/mtxl.jpg) !important;
            }
        }

        &_lx {
            background-image: url(../img/lx/mlx-bg1.jpg) !important;
            height: 510px;
            background-attachment: scroll;
        }
        

        &__content {
            padding-top: 45px !important;
        }

        &__suptitle {
            font-size: 18px;
            line-height: 20px;
            margin-bottom: 10px;
        }

        &__title {
            font-size: 40px;
            line-height: 40px;
        }
    }
}