@import "main-bx";

.benefits {
    display: flex;
    justify-content: space-between;

    &:nth-child(odd) {
        flex-direction: row-reverse;
    }

    &_reverse {
        flex-direction: row-reverse;
    }

    &_model {
        .benefits__title {
            margin-bottom: 60px;
        }
    }

    &__pictures {
        &__quality {
            margin: 0 120px;
        }
    }

    .active__item {
        display: block;
    }

    &__item {
        width: 50%;
        position: relative;
        display: none;

        &__quality {
            display: block;
            height: 100%;
            padding-top: 151px;
            padding-bottom: 135px;
            padding-left: 20px;
            padding-right: 20px;
        }

        img {
            height: 100%;
        }

        &_second {
            display: flex;
            flex-direction: column;
            justify-content: center;

            padding: 0 140px;
        }


        &_dark,
        &_black {
            background-color: #30363A;

            .benefits__title,
            .features__head,
            .features__body p {
                color: $white;
            }

            .features__item:not(:last-child) {
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            }
        }

        &_black {
            background-color: #181B1D;
        }

        &_grey,
        &_white {
            background-color: #F2F2F2;

            .benefits__title {
                color: $color_dark;
            }
        }

        &_white {
            background-color: #fff;
        }

        &_pd {
            padding: 140px 140px 80px;
            display: block;
        }
    }

    &__over {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 100%;
        background-color: rgba(24, 27, 29, 0.8);

        text-align: center;
        color: $white;

        display: flex;
        align-items: center;

        &-list {
            width: 100%;
            max-width: 780px;
            margin: 0 auto;

            li {
                display: flex;
                flex-direction: column;
                padding: 60px 0;

                &:not(:last-child) {
                    border-bottom: 1px solid #C78E66;
                }
            }
        }

        &-numb {
            font-weight: 300;
            font-size: 70px;
            line-height: 70px;

            text-transform: uppercase;
            margin-bottom: 10px;
        }

        &-text {
            font-weight: normal;
            font-size: 18px;
            line-height: 18px;

            color: #CACACA;
        }
    }

    &__suptitle {
        font-weight: normal;
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #CACACA;

        margin-bottom: 20px;

        &_dark {
            color: #30363A;
        }
    }

    &__title {
        font-weight: 300;
        font-size: 60px;
        line-height: 60px;
        text-transform: uppercase;

        color: #FFFFFF;
        margin-bottom: 40px;

        &__quality {
            font-size: 60px;
            font-weight: 400;
            padding-top: 0;
            padding-bottom: 30px;
            text-align: center;
            text-transform: uppercase;
        }

        &_dark {
            color: #30363A;
        }
    }

    &__text {
        font-weight: normal;
        font-size: 18px;
        line-height: 32px;

        &__quality {
            padding: 0 90px;
            text-align: center;
            font-size: 18px;
            line-height: 1.5;
        }

        color: #CACACA;

        margin-bottom: 40px;

        &_dark {
            color: #30363A;
        }
    }

    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 250px;

        img {
            width: 18px;
            height: 18px;
            transform: rotate(-90deg);
            margin-left: 20px;
            margin-top: -5px;
        }
    }

    &__list {

        li {
            display: flex;
            align-items: center;
            padding: 20px 0;
            border-bottom: 1px solid #DEDEDE;
        }

        img {
            margin-right: 10px;
            width: 20px;
            height: 21px;
        }

        span {
            font-weight: 600;
            font-size: 16px;
            line-height: 18px;
            color: #30363A;
        }

        &-icon2 {
            height: 19px !important;
        }
    }
}

@include below(1765px) {
    .benefits {

        &__item {
            &_second {
                padding: 70px 90px;
            }
        }
    }
}

@include below(1366px) {
    .benefits {
        &__suptitle {
            font-size: 16px;
            line-height: 16px;
        }

        &__title {
            font-size: 40px;
            line-height: 40px;
            margin-bottom: 30px;

            &_dark {
                margin-bottom: 24px;
            }
        }

        &__text {
            font-size: 14px;
            line-height: 24px;
            margin-bottom: 30px;
        }

        &__item {
            &_second {
                padding: 0 85px;
            }
        }

        &__btn {
            font-size: 9px;
            line-height: 18px;
            max-width: max-content;
            width: auto;
            padding: 12px 20px;

            img {
                width: 14px;
                height: 14px;
            }
        }

        &__list {
            li {
                padding: 15px 0;
            }

            span {
                font-size: 14px;
                line-height: 18px;
            }
        }

        &__over {

            li {
                padding: 30px 0;
            }

            &-numb {
                font-size: 50px;
                line-height: 50px;
            }

            &-text {
                font-size: 14px;
                line-height: 14px;
            }
        }
    }
}

@include below(1765px) {
    .benefits {

        &__item {

            &_pd {
                padding: 90px 90px 30px;
            }
        }
    }
}

@include below(1250px) {
    .benefits {

        &_model {
            .benefits__title {
                margin-bottom: 30px;
            }
        }

        &__item {
            &_second {
                padding: 50px 30px;
            }

            &_pd {
                padding: 50px;
            }
        }
    }
}

@include below(768px) {
    .benefits {
        flex-wrap: wrap;

        &_first {
            flex-direction: column-reverse;
        }

        &__suptitle {
            font-size: 12px;
            line-height: 15px;
            margin-bottom: 10px;
        }

        &__title {
            font-size: 28px;
            line-height: 30px;
            
            &_dark {
                margin-bottom: 30px;
            }

            &__quality {
                font-size: 28px;
                line-height: 30px;
                text-align: left;
            }
        }

        &__text {
            &__quality {
                display: none;
            }
        }

        &__item {
            width: 100%;

            &_second {
                padding: 40px 20px;
            }

            &_img {
                height: 568px;
            }

            &_pd {
                padding: 40px 20px 20px;
            }

            &_black,
            &_dark {
                order: -1;
            }

            &__quality {
                padding: 40px 20px;
            }
        }

        &__over {
            &-list {
                max-width: 300px;
            }

            &-numb {
                font-size: 30px;
                line-height: 30px;
            }
        }

        &__pictures__flag {
            width: 200px;
            height: 70px;
            display: flex;
            align-items: center;
            margin: 0;

            img {
                height: auto;
            }
        }

        &__pictures__quality {
            margin: 0;
        }
    }
}