@import "main-bx";

.footer-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 0;
    &__left {
        display: flex;
        align-items: center;

        p {
            margin-right: 80px;
        }

        a {
            font-weight: 500;
            font-size: 12px;
            line-height: 18px;

            color: #fff;

            &:hover {
                opacity: 0.7;
            }
        }
    }

    p,
    a {
        font-weight: 500;
        font-size: 12px;
        line-height: 18px;
        color: #CACACA;
    }
}

@include below(768px) {
    .footer-bottom {
        padding: 20px 0;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        &__left {
            flex-direction: column;
            align-items: flex-start;

            p {
                margin-right: 0;
            }

            margin-bottom: 15px;
        }
    }
}