@mixin h1() {
    margin-top: 20px;
    margin-bottom: 20px;
}


@mixin block-bg($url) {
    background-image: url($url);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
}

@mixin below($width) {
    @media (max-width: $width) {
        @content
    }
}