@import "main-bx";

.footer-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    &__left {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
    }

    &__title {
        font-weight: normal;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: 1px;
        text-transform: uppercase;
        margin-right: 30px;
        color: #fff;
    }

    &__nav {
        display: flex;
        align-items: center;
        margin-right: -20px;

        a {
            padding: 0 20px;

            font-weight: 500;
            font-size: 12px;
            line-height: 18px;

            letter-spacing: 2px;
            text-transform: uppercase;
            color: #fff;

            &.active {
                color: #7D8082;
            }

            &:hover {
                opacity: 0.7;
            }
        }
    }
}

@include below(1366px) {
    .footer-top {
        &__nav {
            margin-right: -10px;

            a {
                padding: 0 10px;
            }
        }
    }
}

@include below(1200px) {
    .footer-top {
        align-items: flex-start;

        &__left {
            width: 50%;
        }

        &__nav {
            flex-wrap: wrap;

            a {
                width: 50%;
                padding-bottom: 10px;
            }
        }
    }
}

@include below(768px) {
    .footer-top {
        &__left {
            width: 100%;
            flex-direction: column;
            align-items: flex-start;
        }

        &__nav {
            justify-content: space-between;
            a {
                width: 43%;
                padding: 0 0 10px;
            }
        }
    }
}