@import "main-bx";

.footer-social {

    a {
        display: inline-block;
        &:hover svg {
            fill: #555759;
        }

        &:not(:last-child) {
            margin-right: 20px;
        }
    }
    svg {
        width: 100%;
        max-width: 50px;
        height: 50px;
        fill: #181B1D;
        transition: all .2s linear;
    }
}