@import "main-bx";

.lx-slide {
    font-family: $font_Montserrat !important;
    &__block {
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        background-attachment: fixed;
        width: 100%;
        height: 1080px;

        &_first {
            background-image: url(../img/lx/slides/lx-slider1.jpg);
            
            @include below(768px) {
                background-image: url(../img/lx/slides/mlx-slider1.jpg);
                height: 640px;
            }
        }
    
        &_second {
            background-image: url(../img/lx/slides/lx-slider2.jpg);

            @include below(768px) {
                background-image: url(../img/lx/slides/mlx-slider2.jpg);
            }
        }
    
        &_third {
            background-image: url(../img/lx/slides/lx-slider3.jpg);
            @include below(768px) {
                background-image: url(../img/lx/slides/mlx-slider3.jpg);
            }
        }
    
        &_fourth {
            background-image: url(../img/lx/slides/lx-slider4.jpg);

            @include below(768px) {
                background-image: url(../img/lx/slides/mlx-slider4.jpg);
            }
        }
    
        &_fifth {
            background-image: url(../img/lx/slides/lx-slider5.jpg);
            @include below(768px) {
                background-image: url(../img/lx/slides/mlx-slider5.jpg);
            }
        }
    
        &_sixth {
            background-image: url(../img/lx/slides/lx-slider6.jpg);

            @include below(768px) {
                background-image: url(../img/lx/slides/mlx-slider6.jpg);
            }
        }

        @include below(1600px) {
            height: 900px;
        }

        @include below(768px) {
            height: 510px;
            background-attachment: scroll;
        }
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        padding: 110px 0 90px;

        @include below(1200px) {
            padding-top: 110px;
            padding-bottom: 90px;
        }

        @include below(768px) {
            padding-top: 40px;
            padding-bottom: 30px;
        }

        @include below(400px) {
            padding-top: 40px;
            padding-bottom: 30px;
        }
    }

    
    &__inner {
        display: flex;
        justify-content: flex-end;
        padding: 110px;

        @include below(1200px) {
            padding: 110px 60px;
        }

        @include below(768px) {
            display: block;
            height: 100%;
            padding: 40px 20px;
        }
    }

    &__content {
        width: 100%;
        max-width: 550px;

        @include below(1025px) {
            max-width: 400px;
        }

        @include below(768px) {
            max-width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__title {
        font-weight: 300;
        font-size: 60px;
        line-height: 60px;
        text-transform: uppercase;
        color: #FFFFFF;
        margin-bottom: 40px;

        @include below(1600px) {
            font-size: 40px;
            line-height: 40px;
            margin-bottom: 20px;
        }

        @include below(768px) {
            font-size: 30px;
            line-height: 30px;
            margin-bottom: 0;
            text-align: center;
        }
    }

    &__text {
        font-weight: normal;
        font-size: 18px;
        line-height: 32px;
        color: #FFFFFF;
        margin-bottom: 40px;

        @include below(1600px) {
            font-size: 15px;
            line-height: 28px;
            margin-bottom: 20px;
        }

        @include below(768px) {
            display: none;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        padding: 16px 30px;
        cursor: pointer;
        background-color: #FFFFFF;
        transition: all .2s linear;

        @include below(768px) {
            background-color: transparent;
            border: 1px solid #fff;
        }

        &:hover {
            background-color: #C78E66;

            span {
                color: #FFFFFF;
            }

            svg {
                stroke: #FFFFFF;
            }
        }

        &:active {
            background-color: #FFFFFF;

            span {
                color: #30363A;
            }

            svg {
                stroke: #C78E66;
            }
        }

        span {
            font-weight: 600;
            font-size: 11px;
            line-height: 18px;
            text-align: center;
            letter-spacing: 2px;
            text-transform: uppercase;
            color: #30363A;
            margin-right: 20px;

            @include below(768px) {
                color: #fff;
            }
        }
        svg {
            width: 17px;
            height: 17px;
            stroke: #C78E66;
        }
    }

    &__years {
        width: 100%;
        max-width: 255px;
        margin-left: auto;

        @include below(1600px) {
            max-width: 200px;
        }
        
        @include below(768px) {
            max-width: 123px;
            margin: 0 auto;
        }
    }

    &__avail {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        height: 100%;

        @include below(768px) {
            flex-wrap: wrap;
            height: auto;
            margin-top: -38px;
            align-items: center;
        }

        &-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 25%;
            &:not(:last-child) {
                border-right: 1px solid rgba(255, 255, 255, 0.2);

                @include below(768px) {
                    border: 0;
                }
            }

            @include below(768px) {
                width: 40%;
                margin-top: 38px;
                margin-bottom: auto;
            }
        }

        &-icon {
            width: 60px;
            height: 60px;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 10px;
            svg {
                width: 20px;
                height: 20px;
                fill: #fff;
            }

            @include below(1600px) {
                width: 40px;
                height: 40px;
            }
        }

        &-numb {
            font-weight: 300;
            font-size: 80px;
            line-height: 80px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF;
            margin-bottom: 10px;

            @include below(1600px) {
                font-size: 60px;
                line-height: 60px;
            }

            @include below(768px) {
                font-size: 40px;
                line-height: 40px;
            }
        }
    
        &-text {
            font-weight: normal;
            font-size: 14px;
            line-height: 18px;
            text-align: center;
            color: #7D8082;

            @include below(768px) {
                color: #CACACA;
            }
        }
    }
}
