@import "main-bx";

.video-lx {
    background-image: url(../img/lx/lx-bg2.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    height: 1080px;

    font-family: $font_Montserrat;
    position: relative;

    @include below(1600px) {
        height: 800px;
    }

    @include below(768px) {
        height: 550px;
        background-image: url(../img/lx/mlx-bg2.jpg);
        background-attachment: scroll;
    }

    &__content {
        padding-top: 150px;

        @include below(1600px) {
            padding-top: 100px;
        }

        @include below(1200px) {
            padding-top: 80px !important;
        }

    }

    &__logo {
        width: 100%;
        max-width: 394px;
        margin-bottom: 40px;
        @include below(1600px) {
            max-width: 300px;
            margin-bottom: 20px;
        }

        @include below(768px) {
            max-width: 200px;
            opacity: 0.5;
            margin-bottom: 80px;
        }
    }

    &__text {
        width: 100%;
        max-width: 540px;
        p {
            font-weight: normal;
            font-size: 18px;
            line-height: 32px;
            color: #FFFFFF;
        }

        @include below(768px) {
            display: none;
        }
    }

    &__btn {
        display: flex;
        justify-content: center;
        margin: 17px 0 140px;

        @include below(1600px) {
            margin: 17px 0 40px;
        }
    }

    &__play {
        width: 120px;
        height: 120px;
        background: #FFFFFF;
        border-radius: 60px;
        transition: all .2s linear;
        cursor: pointer;
        &:hover {
            background-color: #C78E66;

            svg {
                fill: #fff;
            }
        }

        &:active {
            background-color: #fff;

            svg {
                fill: #C78E66;
            }
        }

        svg {
            width: 33px;
            height: 40px;
            fill: #C78E66;
            margin-right: -7px;
            @include below(1600px) {
                width: 23px;
                height: 30px;
            }

            @include below(768px) {
                width: 16.5px;
                height: 20px;
                margin-bottom: -5px;
                margin-right: -3px;
            }
        }

        @include below(1600px) {
            width: 80px;
            height: 80px;
        }

        @include below(768px) {
            width: 60px;
            height: 60px;
        }
    }

    &__title {
        position: relative;
        padding-bottom: 30px;
        margin-bottom: 30px;
        h2 {
            font-weight: 300;
            font-size: 70px;
            line-height: 70px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF;

            @include below(1600px) {
                font-size: 50px;
                line-height: 50px;
            }

            @include below(768px) {
                font-size: 30px;
                line-height: 30px;
            }
        }

        &::before {
            content: "";
            position: absolute;
            right: 0;
            left: 0;
            bottom: 0;
            width: 110px;
            margin: 0 auto; 
            height: 1px;
            background-color: #C78E66;
        }

        @include below(768px) {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
    }

    &__date {
        font-weight: normal;
        font-size: 35px;
        line-height: 35px;
        text-align: center;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #FFFFFF;

        @include below(768px) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    &__arrow {
        a {
            @include below(768px) {
                width: 100%;
                display: flex;
                justify-content: space-between;
                padding-left: 20px;
                padding-right: 20px;
            }
        }
        p {
            display: none;
            font-size: 16px;
            line-height: 16px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: #CACACA;
            @include below(768px) {
                display: block;
            }
        }
    }
}