@import "main-bx";

.model {

    &__container {
        padding: 80px 112px;

        @include below(769px) {
            padding: 40px 20px;
        }
    }

    &__block {
        display: block;
    }

    &__title {
        font-weight: 300;
        font-size: 60px;
        line-height: 60px;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 20px;

        @include below(769px) {
            font-size: 28px;
            line-height: 40px;
            margin-bottom: 10px;
        }
    }

    &__text {
        font-weight: normal;
        font-size: 20px;
        line-height: 20px;
        text-align: center;
        letter-spacing: 1px;
        text-transform: uppercase;

        color: #7D8082;

        @include below(769px) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    &__img {
        width: 100%;
        max-width: 1100px;
        margin: 31px auto;

        @include below(769px) {
            max-width: 100%;
            margin: 20px 0;
        }
    }

    &__avail {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        @include below(769px) {
            margin-top: -20px;
        }

        &-item {
            display: flex;
            align-items: center;
            flex-direction: column;
            flex: 1 0 auto;

            &:not(:last-child) {
                border-right: 1px solid #CACACA;

                
            }

            @include below(769px) {
                flex-basis: 50%;
                margin-top: 20px;
                padding: 20px;
            }

            &:nth-child(2) {
                @include below(769px) {
                    border-right: 0;
                }
            }
        }

        &-icon {
            width: 60px;
            height: 60px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #F2F2F2;
            margin-bottom: 10px;

            svg {
                width: 20px;
                height: 20px;
                fill: #C78E66;
            }
        }

        &-numb {
            font-weight: 300;
            font-size: 80px;
            line-height: 80px;
            text-align: center;
            text-transform: uppercase;
            margin-bottom: 10px;

            @include below(769px) {
                font-size: 40px;
                line-height: 40px;
            }
        }

        &-text {
            font-weight: normal;
            font-size: 14px;
            line-height: 18px;
            text-align: center;

            color: #30363A;
        }
    }
}

@import "model-tab";