@import "main-bx";

.auto-stock {
    padding: 112px 0;
    &_lx {
        font-family: $font_Montserrat !important;
        padding: 150px 0 85px;
        position: relative;
        z-index: 100;
        background-color: #fff;
    }
    &__flex {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        &-item {
            font-family: $font_Montserrat;
            color: #30363A;

            width: 48%;
            transition: all .2s linear;
            
            &.blur {
                opacity: 0.7;
                filter: blur(5px);
            }
        }
    }
    
    &__cost {
        display: flex;
        justify-content: center;
        margin: 10px 0 30px;
        font-size: 24px;
        line-height: 32px;

        &_uppercase {
            text-transform: uppercase;
        }
        @media (max-width: 930px) and (min-width: 768px) {
            flex-wrap: wrap;
        }
        @media (max-width: 650px)  {
            text-align: center;
            font-size: 16px;
            line-height: 22px;
            justify-content: center;
        }

        &-item {
            
            @media (max-width: 1380px) {
                text-align: center;
            }
            @media (max-width: 930px) and (min-width: 768px) {
                width: 100%;
            }
            &:first-child {
                margin-right: 40px;
                @media (max-width: 930px) and (min-width: 768px) {
                    margin-right: 0;
                    margin-bottom: 30px;
                }
                @media (max-width: 650px) {
                    margin-right: 0;
                    margin-bottom: 10px;
                }
            }
            
            strong {
                display: inline-block;
                font-size: 28px;
                line-height: 32px;
                margin-left: 20px;

                @media (max-width: 650px) {
                    font-size: 20px;
                    line-height: 22px;
                    margin-left: 10px;
                }
            }
        }
    } 

    &__title {
        font-weight: 300;
        font-size: 80px;
        line-height: 80px;

        text-align: center;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    &__name {
        font-weight: 300;
        font-size: 50px;
        line-height: 50px;
        text-align: center;
        text-transform: uppercase;
        color: #30363A;
        margin-bottom: 35px;

        @include below(1399px) {
            font-size: 35px;
            line-height: 35px;
        }

        @include below(768px) {
            font-size: 25px;
            line-height: 30px;
            margin-bottom: 30px;
        }
    }

    &__text {        
        font-weight: normal;
        font-size: 20px;
        line-height: 20px;

        text-align: center;
        letter-spacing: 1px;
        text-transform: uppercase;

        color: #7D8082;

        margin-bottom: 20px;
    }


    &__img {
        margin-bottom: 20px;
    }

    &__price {
        font-weight: 600;
        font-size: 40px;
        line-height: 50px;
        text-align: center;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #C78E66;
        padding-top: 30px;

        @include below(1399px) {
            font-size: 25px;
            line-height: 30px;
        }

        @include below(768px) {
            padding-top: 10px;
            font-size: 18px;
            line-height: 20px;
        }
    }

    &__avail {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: -40px;
        &-item {
            width: 50%;
            display: flex;
            flex-direction: column;
            align-items: center;

            margin-top: 40px;

            &:first-child {
                border-right: 1px solid #DEDEDE;
            }

            &:nth-child(3) {
                border-right: 1px solid #DEDEDE;
            }
        }
        &-icon {
            
            svg {
                fill: #C78E66;
                width: 20px;
                height: 20px;
            }
        }

        &-key {
            font-weight: 300;
            font-size: 80px;
            line-height: 80px;
            text-transform: uppercase;

            margin: 10px 0;
        }

        &-value {
            font-weight: normal;
            font-size: 18px;
            line-height: 18px;
        }
    }

    &__btn {
        display: flex;
        justify-content: center;
        margin-bottom: 40px;
        
        .btn {
            display: flex;
            align-items: center;
            padding: 16px 30px;
            margin-top: 0;
            background: #30363A;
            color: #fff;

            &:hover {
                background: #4d5155;

                svg {
                    fill: #fff;
                }
            }
        }

        .auto-stock__btn-white {
            border: 1px solid #30363A;
            background-color: #fff;
            color: #30363A;

            &:hover {
                background: #C78E66;
                border-color: #C78E66;
                color: #fff;
                svg {
                    fill: #fff;
                }
            }
        }

        svg {
            width: 10px;
            height: 18px;
            margin-left: 20px;
            fill: #C78E66;
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 70px;
        .btn {
            margin: 0 10px;
            @include below(768px) {
                width: 100%;
                margin: 0;
                justify-content: space-between;
            }
        }

        @include below(768px) {
            flex-direction: column;
            margin-top: 40px;
        }

        .auto-stock__btn {
            @include below(768px) {
                width: 100%;
                max-width: 280px;
                margin-bottom: 20px;
            }
        }
    }

    &__addon {
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        text-align: center;
        color: #7D8082;
    }
}


@include below(1366px) {
    .auto-stock {
        padding: 80px 0;

        &__title {
            font-size: 60px;
            line-height: 60px;
        }

        &__text {
            font-size: 16px;
            line-height: 16px;
        }

        &__avail {
            &-key {
                font-size: 60px;
                line-height: 60px;
            }
            &-value {
                font-size: 14px;
                line-height: 14px;
            }
        }
    }
}

@include below(768px) {
    .auto-stock {
        padding: 40px 0;
        &__flex {
            flex-direction: column;
            
            &-item {
                width: 100%;
                &:first-child {
                    padding-bottom: 60px;
                    border-bottom: 1px solid #DEDEDE;
                }

                &:last-child {
                    padding-top: 40px;
                }
            }
        }

        &__title {
            font-size: 40px;
            line-height: 40px;
            margin-bottom: 10px;
        }

        &__text {
            line-height: 20px;
        }

        &__avail {
            margin-top: -20px;
            text-align: center;
            &-item {
                margin-top: 20px;
                padding: 0 10px;
            }
            &-key {
                font-size: 40px;
                line-height: 40px;
                margin-bottom: 10px;
            }
        }
    }
}