@import "main-bx";

.map {
    display: flex;
    height: auto;
    max-height: 680px;

    &__container {
        width: 70%;
    }

    &__box {
        width: 30%;
        padding: 60px 50px;

        hr {
            margin: 40px 0 50px 0;
        }
    }

    &__title {
        font-family: $font_Montserrat;
        font-weight: normal;
        font-size: 24px;
        line-height: 100%;
        margin-bottom: 25px;
    }

    &__list {

        li {
            font-family: $font_Montserrat;
            display: flex;
            align-items: center;

            padding-bottom: 20px;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;

            &:last-child {
                img {
                    width: 20px;
                    height: 14px;
                }
            }
        }

        &-text {
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: #7D8082;
        }

        &-tel {
            font-weight: normal;
            font-size: 20px;
            line-height: 20px;
            letter-spacing: 1px;
            text-transform: uppercase;

            color: #30363A;
        }
        
        &-time,
        &-mail {
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: #30363A;
        }

        img {
            width: 20px;
            height: 20px;
            margin-right: 10px;
        }

        a {
            color: $white;
        }
    }

    &__btn {
        margin-top: 10px;
        display: flex;
        align-items: center;
        padding: 16px 30px;

        &:hover svg {
            fill: $white;
        }
        svg {
            width: 10px;
            height: 18px;
            margin-left: 20px;
            fill: $color_burlywood
        }
    }
}

@include below(1765px) {
    .map {
        &__container {
            width: 50%;
        }

        &__box {
            width: 50%;
            padding: 50px 30px 50px 85px;

            hr {
                margin: 30px 0 40px 0;
            }
        }

        &__list {
            
            li {
                padding-bottom: 15px;
            }

            &-text {
                font-size: 12px;
                line-height: 16px;
            }

            &-tel {
                font-size: 16px;
                line-height: 20px;
            }

            &-time {
                font-size: 12px;
                line-height: 20px;
            }

            &-mail {
                font-size: 12px;
                line-height: 20px;
            }
        }

        &__btn {
            font-size: 11px;
            line-height: 18px;
            width: auto;
            padding: 12px 30px;
            svg {
                width: 6px;
                height: 14px;
            }
        }
    }
}

@include below(1250px) {
    .map {
        &__box {
            padding: 50px 20px 50px 30px;
        }
    }
}

@include below(map-get($breaks, tablet-md)) {
    .map {
        flex-wrap: wrap;
        max-height: none;

        &__container {
            width: 100%;
            height: 400px;
        }

        &__box {
            padding: 50px 20px;
            width: 100%;

            .btn {
                font-size: 9px;
                line-height: 18px;
                padding: 12px 20px;
            }
        }
        &__title {
            font-size: 18px;
            line-height: 20px;
            margin-bottom: 20px;
        }
    }
}