@import "main-bx";

.mfp-content .modal-car {
    display: block;
}

.mfp-container {
    padding: 0 !important;
    
}

.mfp-close {
    display: block !important;
    font-size: 100px !important;
    font-family: Montserrat !important;
    font-weight: 200;
    color: #C78E66 !important;
    right: 40px !important;
    top: 40px !important;

    @include below(768px) {
        font-size: 50px !important;
        right: 0 !important;
        top: 0 !important;
    }
}


.modal-car {
    display: none;
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    height: 100%;
    background-color: #181B1D;
    font-family: $font_Montserrat !important;
    &__inner {
        padding: 50px 120px;

        @include below(768px) {
            padding: 20px 0;
        }
    }

    &__title {
        font-weight: normal;
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #7D8082;

        padding-bottom: 50px;

        @include below(1399px) {
            padding-bottom: 25px;
        }

        @include below(768px) {
            padding: 0 20px 22px;
        }
    }

    &__slider {
        margin-bottom: 32px;
        margin-right: -120px;

        @include below(1399px) {
            margin-bottom: 15px;
            margin-right: -50px;
        }

        @include below(1100px) {
            margin-right: 0;
        }

        @include below(768px) {
            margin-bottom: 20px;
        }

        &-img {
            margin-bottom: 30px;
            height: 500px;
            img {
                height: 100%;
            }

            @include below(1600px) {
                height: 375px;
            }

            @include below(1399px) {
                height: 275px;
            }

            @include below(1100px) {
                height: 375px;
            }

            @include below(768px) {
                height: 320px;
                margin-bottom: 20px;
            }
        }

        &-item {
            margin-right: 120px;
            position: relative;
            @include below(1399px) {
                margin-right: 50px;
            }

            @include below(1100px) {
                margin-right: 0;
            }
        }

        &-title {
            font-weight: 300;
            font-size: 40px;
            line-height: 40px;
            text-transform: uppercase;
            color: #FFFFFF;
            padding-bottom: 30px;
            margin-bottom: 30px;
            position: relative;

            &::before {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                height: 1px;
                width: 110px;
                background-color: #C78E66;

                @include below(768px) {
                    content: none;
                }
            }


            @include below(1600px) {
                font-size: 20px;
                line-height: 20px;
            }

            @include below(1399px) {
                padding-bottom: 15px;
                margin-bottom: 15px;
            }

            @include below(768px) {
                font-size: 18px;
                line-height: 20px;
                padding-left: 20px;
                display: none;
            }

            &_mobile {
                display: none;
                @include below(768px) {
                    display: block;
                }
            }
        }

        &-text {
            font-weight: normal;
            font-size: 18px;
            line-height: 27px;
            color: #FFFFFF;

            @include below(1600px) {
                font-size: 15px;
                line-height: 25px;
            }

            @include below(768px) {
                font-size: 14px;
                line-height: 20px;
                padding-left: 20px;
            }
        }


        &-list {
            list-style-type: disc;
            padding-left: 20px;
        }
        
    }

    &__bottom {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__nav {
        width: 60px;
        height: 60px;
        background: #C78E66;
        border-radius: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        @include below(768px) {
            width: 40px;
            height: 40px;
        }

        &.slick-disabled {
            background: #30363A;
            svg {
                opacity: 0.5;
            }
        }

        &:first-child {
            svg {
                transform: rotate(-180deg);
            }
        }

        svg {
            width: 12px;
            height: 22px;
            fill: #fff;

            @include below(768px) {
                width: 8px;
                height: 15px;
            }
        }
    }

    &__page {
        font-weight: normal;
        font-size: 18px;
        line-height: 18px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #FFFFFF;
        margin: 0 30px;

        @include below(768px) {
            font-size: 12px;
            line-height: 18px;
            margin: 0 20px;
        }
    }
}