@import "main-bx";

.instagram {
    
    &__title {
        font-weight: 300;
        font-size: 40px;
        line-height: 40px;
        text-align: center;
        text-transform: uppercase;

        padding: 100px 0;
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__img {
        width: 25%;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 100;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 40px;
        &-top {
            display: flex;
            align-items: center;
        }

        &-item {
            display: flex;
            align-items: center;
            padding: 0 10px;
            svg {
                width: 20px;
                height: 20px;
                margin-right: 10px;
            }

            span {
                font-weight: normal;
                font-size: 14px;
                line-height: 18px;
                color: #CACACA;
            }
        }

        &-text {
            margin-top: 30px;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #fff;
            text-align: center;
        }
    }
}

@include below(1366px) {
    .instagram {
        &__title {
            padding: 70px 0;
            font-size: 40px;
            line-height: 40px;
        }

        &__content {
            &-text {
                font-size: 12px;
                line-height: 16px;
            }
        }
    }
}

@include below(768px) {
    .instagram {
        &__title {
            font-size: 30px;
            line-height: 30px;
            padding: 40px 0;
        }

        &__img {
            width: 50%;
            &_delete {
                display: none;
            }
        }
    }
}