@import "main-bx";

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    padding: 25px 0;
    background-color: $color_dark;

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 40px;

        width: 100%;
        max-width: 1920px;
        margin: 0 auto;

        &_nav {
            display: none;
        }
    }

    &__item {
        height: 34px;
        display: flex;
        align-items: center;
        color: $white;

        .header__burger {
            display: none;
        }

        &:nth-child(2) {
            width: 50%;
            justify-content: flex-end;
        }
    }

    &__title {
        font-family: $font_Montserrat;
        font-weight: normal;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: 1px;
        text-transform: uppercase;
        margin: 0 20px;

        @include below(360px) {
            font-size: 8px;
        }
    }

    &__logo {
        border-right: 1px solid #C78E66;
        padding-right: 40px;
        margin-right: 40px;
    }

    &__icon {
        width: 18px;
        height: 18px;
    }

    &__contacts {

        border-right: 1px solid rgba(255, 255, 255, 0.2);
        padding: 5px 0;
        padding-right: 20px;

        &-item {
            color: #fff;
            font-weight: normal;
            font-size: 20px;
            line-height: 20px;

            letter-spacing: 1px;
            text-transform: uppercase;
        }
    }

    &__icons {
        display: flex;
        align-items: center;

        &-item {
            display: flex;
            align-items: center;
            color: #FFFFFF;
            transition: all .2s linear;
            &:hover {
                color: #C78E66;
            }

            span {
                font-weight: 500;
                font-size: 9px;
                line-height: 18px;
                letter-spacing: 2px;
                text-transform: uppercase;
                margin-right: 10px;
            }

            &:first-child {
                margin-right: 25px;
            }
            
            svg {
                width: 18px;
                height: 18px;
                stroke: currentColor;
            }   
        }

        &-user {
            stroke: none;
            fill: currentColor;
        }
    }

    &__call {
        margin-left: 30px;
        &.btn {
            padding: 11px 20px;
            border: 1px solid #C78E66;
        }
    }

    &__anchors {
        display: flex;
        align-items: center;

        a {
            display: inline-block;
            padding: 0 15px;
            font-weight: 600;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: $white;
            transition: all .2s linear;
            white-space: nowrap;
            &:hover {
                color: #7D8082;
            }

            &.active {
                color: #7D8082;
            }
        }
    }

}

@include below(1366px) {
    .header {
        padding: 15px 0;

        &__contacts {
            &-item {
                font-size: 16px;
                line-height: 16px;
            }
        }

        &__icon {
            width: 16px;
            height: 16px;
        }
    }
}


@include below(1250px) {
    .header {
        padding: 0;

        &__inner {
            flex-wrap: wrap;
            padding: 0;

            &_nav {
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1100;
                width: 100%;
                height: 100vh;
                background: $white;
                color: $color_dark;
                flex-direction: column;
                justify-content: flex-start;
                transform: translateX(200%);
                transition: all .2s linear;
                padding: 25px 10px !important;

                &.show {
                    transform: translateX(0%);
                }

                .header__logo {
                    margin-bottom: 20px;
                }
            }
        }

        &__logo {
            border-right: none;
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: space-between;
            img {
                max-width: 128px;
            }
        }

        &__item {
            height: auto;
            width: 100%;

            &:nth-child(1) {
                padding: 15px 20px;
                width: 100%;

                display: flex;
                justify-content: space-between;
                align-items: center;

                .header__title {
                    display: none;
                }

                .header__anchors {
                    display: none;
                }

                .header__burger {
                    display: block;
                }
            }

            &:nth-child(2) {
                display: none;
            }
        }

        &__nav {
            a {
                display: block;
                font-family: $font_Montserrat;
                font-size: 14px;
                line-height: 17px;
                margin-bottom: 20px;
                text-transform: uppercase;
                color: $color_dark;

                &:hover {
                    color: #7D8082;
                }

                &.active {
                    color: #7D8082;
                }
            }

        }

        &__title {
            font-size: 10px;
            line-height: 15px;
        }

        &__contacts {
            border-right: none;
            padding: 0;
            &_mob {
                width: 20px;
            }
            &-item {
                display: flex;
                color: $color_dark;
                font-size: 11px;
                line-height: 13px;
                text-transform: unset;

                img {
                    margin-right: 20px;
                }
            }

            p {
                margin-bottom: 20px;
            }
        }

        &__menu {
            margin-bottom: 20px;
        }

        &__menu-title {
            font-weight: 600;
            font-size: 18px;
            line-height: 160%;
        }

        &__menu-text {
            font-weight: normal;
            font-size: 12px;
            line-height: 15px;
        }
    }
}