@import "main-bx";

.sidebar {
    
    position: absolute;
    top: 110px;
    left: 120px;


    @include below(1200px) {
        top: 60px;
        left: 60px;
    }

    @include below(768px) {
        display: none;
    }

    ul {
        border-left: 1px solid rgba(196, 196, 196, 0.3);
    }

    &__link {
        font-weight: normal;
        font-size: 14px;
        line-height: 14px;
        letter-spacing: 1px;
        text-transform: uppercase;

        color: #FFFFFF;

        opacity: 0.75;

        transition: all .2s linear,   font-size .5s linear;

        position: relative;

        @include below(1600px) {
            font-size: 12px;
            line-height: 12px;
        }

        &.active {
            font-size: 20px;
            line-height: 20px;
            opacity: 1;
            &::before {
                opacity: 1;
            }
            @include below(1600px) {
                font-size: 15px;
                line-height: 15px;
            }
        }

        &:not(:last-child) {
            margin-bottom: 5px;
        }
        a {
            color: currentColor;
            display: block;
            padding: 13px 43px;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: -2.5px;
            width: 5px;
            height: 100%;
            background: #C78E66;
            border-radius: 3px;
            opacity: 0;
            transition:  all .2s linear;
        }
    }
    
}
