@import "main-bx";

*, *::after, *::before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}


body {
    font-family: $font_Montserrat;
    font-size: 16px;
    color: $color_dark;
    padding: 0;
    margin: 0;
    background-color: $white;
    -webkit-font-smoothing: antialiased;
    width: 100%;
    max-width: $body_max_width;
    margin: 0 auto;
}

.container {
    width: 100%;
    max-width: map-get($container,  desktop);
    margin: 0 auto;

    @include below(1366px) {
        max-width: 1206px;
    }

    @include below(1200px) {
        max-width: 100%;
        padding: 0 25px;
    }

    @include below(400px) {
        padding: 0 10px;
    }

}

.main {
    overflow: hidden;
    padding-top: 84px;
    position: relative;

    @include below(1366px) {
        padding-top: 60px;
    }

    @include below(1024px) {
        padding-top: 60px;
    }

    @include below(768px) {
        margin-bottom: 0;
    }

    &_top {
        margin-bottom: 0 !important;
    }

    &_bottom {
        padding-top: 0 !important;
    }
}

ul {
    list-style-type: none;
}

button,
input {
    background: transparent;
    border: none;
}

a {
    text-decoration: none;
}

img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}


.fadeIn {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
@-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

svg {
    // transition: all .2s linear;
}

.video {
    position: relative;
    &__player {
        width: 100%;
    }

    & .video__anchor {
        bottom: 5px;

        @media (max-width: 768px)  {
            display: none;
        }
    }
}


.anchor {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    display: flex;
    justify-content: center;

    a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        background: #30363A;
        transition: all .2s linear;

        &:hover {
            background: #282c2e;
        }

        @media (max-width: 1366px) {
            width: 40px;
            height: 40px;
        }
    }

    svg {
        width: 18px;
        height: 18px;

        @media (max-width: 1366px) {
            width: 14px;
            height: 14px;
        }
    }
}
